import React, { useCallback, useEffect, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { connect, useDispatch } from 'react-redux';
import { first } from 'rxjs/operators';
import { UploadIcon } from '../../../assets/images/icons';
import { setAuthenticatedUser } from '../../../reduxStore/actions/authActions';
import { getAuthenticatedUser, updateUserById, updateUserProfilePicture, changeUsersPassword, removeUserbyId } from '../../../services/user.service';
import { uncachedUrl } from '../../../shared/utility';
import { Button, ModalR, UnfocusableWrapper } from '../../common';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import cls from './MyProfile.module.scss';
import { isMobile } from 'react-device-detect';
import { setShowMenu } from '../../../reduxStore/actions/flowActions';
import { useLocation, useNavigate } from 'react-router-dom';
import UserOverviewModal from '../../SideMenuEntries/CompanyComps/UserOverviewModal';

const MyProfile = props => {
	const { authenticatedUser, user, components } = props;
	const [avatarError, setAvatarError] = useState('');
	const [authenticUser, setAuthenticUser] = useState(authenticatedUser);

	const [actualPassword, setActualPassword] = useState('');
	const [actualPasswordError, setActualPasswordError] = useState('');
	const [newPassword, setNewPassword] = useState('');
	const [confirmNewPassword, setConfirmNewPassword] = useState('');
	const [confirmationError, setConfirmationError] = useState('');
	const [deleteUser, setDeleteUser] = useState(false);

	const [loading, setLoading] = useState(false);
	const [showTasks, setShowTasks] = useState(false);
	const [showResetSuccessModal, setShowResetSuccessModal] = useState(false);
	const location = useLocation();
	const data = location.state;

	const navigate = useNavigate();

	const dispatch = useDispatch();

	const onDrop = useCallback(
		acceptedFiles =>
			acceptedFiles.forEach(file => {
				setAvatarError('');
				if (file?.type?.toString().indexOf('image/') > -1 && ['jpeg', 'jpg', 'png'].indexOf(file?.type?.toString().split('/')[1]) > -1) {
					let reader = new FileReader();
					reader.readAsDataURL(file);
					reader.onloadend = _e => {
						const formData = new FormData();
						formData.append('avatar', file);
						updateUserProfilePicture(authenticUser.id, file).subscribe({
							next: data => {
								setAuthenticUser(data);
							},
							error: error => {
								setAvatarError(error?.data);
							},
						});
					};
				}
			}),
		[]
	);
	const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

	const [authenticUser_ApiError, setAuthenticUser_ApiError] = useState('');
	const [updateUser_apiError, setUpdateUser_ApiError] = useState('');

	useEffect(() => {
		setAuthenticUser_ApiError('');

		if (data?.user?.id.length > 0) {
			setAuthenticUser(data.user);
			dispatch(setShowMenu(false));
		} else {
			dispatch(setShowMenu(true));
			getAuthenticatedUser()
				.pipe(first())
				.subscribe({
					next: data => {
						setAuthenticUser(data);
					},
					error: error => {
						setAuthenticUser_ApiError(error.data);
					},
				});
		}
	}, []);

	const [fieldsErrors, setFieldErrors] = useState({ first_name: '', last_name: '' });
	const [elementsTouched, setElementsTouched] = useState({ first_name: false, last_name: false });

	const onSaveUserData = useCallback(() => {
		if (!Object.values(elementsTouched).some(value => value === true)) {
			return;
		}
		setFieldErrors({ first_name: '', last_name: '' });
		setUpdateUser_ApiError('');

		updateUserById(authenticUser.id, authenticUser.first_name, authenticUser.last_name)
			.pipe(first())
			.subscribe({
				next: () => {
					setElementsTouched({ first_name: false, last_name: false });
				},
				error: error => {
					const foundErrors = { first_name: '', last_name: '' };
					if (error.data.errors.first_name) {
						foundErrors.first_name = error.data.errors.first_name?.join('; ');
					}
					if (error.data.errors.last_name) {
						foundErrors.last_name = error.data.errors.last_name?.join('; ');
					}
					if (error.data.message !== 'The given data was invalid.') {
						setUpdateUser_ApiError(error.data.message);
					}
					setFieldErrors(c => ({ ...c, ...foundErrors }));
				},
			});
	}, [authenticUser, elementsTouched]);
	const renderDropZone = useCallback(() => {
		return (
			<div className={cls.avatarDragContainer} app-drag-active={isDragActive ? 'true' : 'false'} {...getRootProps()}>
				<input {...getInputProps()} className={cls.avatarDragInput} accept="*,image/png, image/jpeg, image/jpg" />
				{isDragActive ? (
					<div className={cls.avatarContentContainer}>
						<UploadIcon className={cls.avatarUploadIcon} />
					</div>
				) : (
					<div className={cls.avatarContentContainer}>
						{authenticUser?.photo?.length > 2 ? (
							<img alt="lg" src={uncachedUrl(authenticUser?.photo)} className={cls.avatarUrlImg} />
						) : (
							<div className={cls.avatarUploadPictureContainer}>
								<UploadIcon className={cls.avatarUploadIcon} />
							</div>
						)}
					</div>
				)}
			</div>
		);
	}, [authenticUser]);

	const onSubmitClick = () => {
		setLoading(true);
		if (newPassword !== confirmNewPassword) {
			setLoading(false);
			return setConfirmationError('Confirmation does not match');
		}
		changeUsersPassword(actualPassword, newPassword, confirmNewPassword, authenticUser.id)
			.pipe(first())
			.subscribe({
				next: () => {
					setLoading(false);
					setShowResetSuccessModal(true);
					setActualPasswordError('');
					setConfirmationError('');
					updateUser_apiError('');
					authenticUser_ApiError('');
				},
				error: error => {
					console.log(error);
					if (error?.data?.errors?.old_password) {
						setActualPasswordError(error?.data?.errors?.old_password[0]);
					}
					setLoading(false);
					setShowResetSuccessModal(false);
				},
				complete: () => {
					setActualPassword('');
					setNewPassword('');
					setConfirmNewPassword('');
				},
			});
	};

	const onDeleteUserClick = () => {
		setLoading(true);

		removeUserbyId(authenticUser.id)
			.pipe(first())
			.subscribe({
				next: () => {
					setLoading(false);
					setDeleteUser(false);
					navigate('/users');
				},
				error: error => {
					console.log(error);
				},
				complete: () => {},
			});
	};

	console.log(components.filter(component => component?.created_by?.id === data?.user?.id).length > 0);
	return (
		<div id="my-profile-settings" className={cls.host}>
			{data?.user?.id?.length > 0 && authenticatedUser.is_admin == true ? (
				<div style={{ width: '70%', display: 'flex', justifyContent: 'space-between' }}>
					<Button title={'Back'} clicked={() => navigate('/users')} style={{ width: '100px' }} />
					{data?.user && <Button title="Show Tasks" loading={loading} light clicked={() => setShowTasks(true)} />}
				</div>
			) : (
				<div style={{ margin: '80px 0 0 0' }}>
					<HeaderTopBar headerTitle={'My Profile'} targetProcessing={false} />
				</div>
			)}

			{isMobile && <LabeledContainer label={'Personal Information'} />}
			{isMobile && (
				<div style={{ display: '', alignSelf: 'center' }}>
					<LabeledField label={'Profile picture'}>
						{renderDropZone()}
						{avatarError?.length > 0 && <p className={cls.avatarErrorText}>{avatarError}</p>}
					</LabeledField>
				</div>
			)}

			<div className={cls.labeledContainerHost} style={{ width: isMobile ? '100%' : '50%' }}>
				<div style={{ display: 'flex', justifyContent: 'space-between' }}>
					{!isMobile && <LabeledContainer label={'Personal Information'} />}
					<div style={{ padding: '0 0 0px 0', display: isMobile ? 'none' : '' }}>
						<LabeledField label={'Profile picture'}>
							{renderDropZone()}
							{avatarError?.length > 0 && <p className={cls.avatarErrorText}>{avatarError}</p>}
						</LabeledField>
					</div>
				</div>
				<div className={cls.userDataWrapper}>
					<UnfocusableWrapper className={cls.userSettingsContainer} onClickOutside={onSaveUserData}>
						<div style={{ width: 'inherit' }}>
							<LabeledField label={'First Name'} error={fieldsErrors.first_name}>
								<input
									type="text"
									name="first-name"
									className={cls.inputField}
									placeholder="First Name"
									value={authenticUser?.first_name ?? ''}
									onChange={e => setAuthenticUser(c => ({ ...c, first_name: e.target.value }))}
									onFocus={() => {
										setElementsTouched(c => ({ ...c, display_name: true }));
									}}
								/>
							</LabeledField>
							<LabeledField label={'Last Name'} error={fieldsErrors.last_name}>
								<input
									type="text"
									name="last-name"
									className={cls.inputField}
									placeholder="Last Name"
									value={authenticUser?.last_name ?? ''}
									onChange={e => setAuthenticUser(c => ({ ...c, last_name: e.target.value }))}
									onFocus={() => {
										setElementsTouched(c => ({ ...c, display_name: true }));
									}}
								/>
							</LabeledField>

							<LabeledField label={'Username'} wrappedContent>
								<p className={cls.emailParagraph}>{authenticUser?.username}</p>
							</LabeledField>

							<LabeledField label={'Work Email'} wrappedContent>
								<p className={cls.emailParagraph}>{authenticUser?.email}</p>
							</LabeledField>

							<LabeledField label={'Position' + (authenticUser.positions?.length > 1 ? 's' : '')} wrappedContent>
								{authenticUser.positions?.length > 0 &&
									authenticUser.positions?.map((i, idx) => (
										<p key={idx} className={cls.positionsParagraph}>
											{i.name}
										</p>
									))}
							</LabeledField>
						</div>

						<div style={{ display: isMobile ? '' : 'flex', gap: '24px', marginTop: '15px', width: '100%' }}>
							<div style={{ flex: 1 }}>
								<LabeledField label={data?.user?.id?.length > 0 && authenticatedUser.is_admin == true ? 'Admin Password' : 'Old Password'} error={fieldsErrors.first_name}>
									<input type="password" name="old-password" className={cls.inputField} value={actualPassword} onChange={e => setActualPassword(e.target.value)} />
								</LabeledField>
							</div>
							<div style={{ flex: 1 }}>
								<LabeledField label={'New Password'} error={fieldsErrors.first_name}>
									<input type="password" name="new-password" className={cls.inputField} value={newPassword} onChange={e => setNewPassword(e.target.value)} />
								</LabeledField>
							</div>
							<div style={{ flex: 1 }}>
								<LabeledField label={'Verify New Password'} error={fieldsErrors.last_name}>
									<input type="password" name="verify-password" className={cls.inputField} value={confirmNewPassword} onChange={e => setConfirmNewPassword(e.target.value)} />
								</LabeledField>
							</div>
						</div>
						<div style={{ display: 'flex' }}>
							{actualPassword.length > 5 && newPassword.length > 5 && confirmNewPassword.length > 5 && (
								<div style={{ display: 'flex', gap: '24px', marginTop: '15px', maxWidth: '100%' }}>
									<Button title="Update password" loading={loading} lighter clicked={onSubmitClick} />
								</div>
							)}
							{user.is_admin == true && data !== null && data?.user.is_admin == false && data?.user.positions.length == 0 && components.filter(component => component?.created_by?.id === data?.user?.id).length === 0 && (
								<div style={{ display: 'flex', gap: '24px', marginTop: '15px', maxWidth: '100%' }}>
									<Button title="Delete User" loading={loading} light clicked={() => setDeleteUser(true)} />
								</div>
							)}
						</div>
						<div>{showResetSuccessModal && <p className={cls.success}>Password Successfully changed</p>}</div>
						<div>{actualPasswordError?.length > 0 && <p className={cls.errorGeneralText}>{actualPasswordError}</p>}</div>
						<div>{confirmationError?.length > 0 && <p className={cls.errorGeneralText}>{confirmationError}</p>}</div>
						<div>{authenticUser_ApiError?.length > 0 && <p className={cls.errorGeneralText}>{authenticUser_ApiError}</p>}</div>
						<div>{updateUser_apiError?.length > 0 && <p className={cls.errorGeneralText}>{updateUser_apiError}</p>}</div>
					</UnfocusableWrapper>
					<ModalR
						shadowedContainerStyle={{ maxWidth: '400px', minWidth: '300px', width: '40%' }}
						onClose={() => {
							setDeleteUser(false);
						}}
						isOpen={deleteUser}
						backdropColor={true}
						blured={true}
					>
						<h1 className={cls.deletePosition_title}>{'Delete User'}</h1>
						<p className={cls.deletePosition_description}>{'Press Delete to continue with the delete of user'}</p>
						<div className={cls.deleteModalContainer}>
							<div className={cls.deletePosition_buttonContainer} style={{ display: 'flex' }}>
								<Button
									title="Close"
									style={{ marginLeft: '0px', width: '79%', padding: '0 10px' }}
									titleStyle={{ textAlign: 'center' }}
									clicked={() => {
										setDeleteUser(false);
									}}
								/>
								<Button title="Delete" style={{ marginLeft: '0px', width: '79%', padding: '0 10px' }} light loading={loading} titleStyle={{ textAlign: 'center' }} clicked={onDeleteUserClick} />
							</div>
						</div>
					</ModalR>
					{showTasks && data?.user && <UserOverviewModal isVisible={showTasks} activeItem={data?.user} handleClose={() => setShowTasks(false)} isAdmin={user.is_admin} />}
				</div>
			</div>
		</div>
	);
};
const LabeledContainer = props => {
	const { label, children, readOnly } = props;
	return (
		<div className={cls.labeledContainerHost} style={{ width: isMobile ? '100%' : '50%' }} app-readonly={readOnly ? 'true' : 'false'}>
			<p className={cls.labeledContainerLabel}>{label}</p>
			{children}
		</div>
	);
};
const LabeledField = props => {
	const { label, error, children, wrappedContent } = props;
	return (
		<div className={cls.labeledFieldHost}>
			<label className={cls.labeledFieldLabel}>{label}</label>
			{wrappedContent ? <div className={cls.labeledFieldChildrenContainer}>{children}</div> : children}

			{error?.length > 0 && <p className={cls.labeledFieldError}>{error}</p>}
		</div>
	);
};

const mapStateToProps = store => {
	const { authenticatedUser } = store.authReducer;
	const { user } = store.sessionReducer;
	const { components } = store.flowReducer;
	return { authenticatedUser, user, components };
};

const mapDispatchToProps = dispatch => {
	return {
		onSetAuthenticatedUserPosition: position => dispatch(setAuthenticatedUser(position)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(MyProfile);
